import axios from 'axios';
// import { API_URL } from '../constants';
// import {_dispatch, _axios, getSessionItem} from '../utilities';



export function getNews(address) {
  try {
    const response = axios({
      method: 'GET',
      //   params: payload,
      //   headers: {Authorization: `${user.token}`},
      url: address,
    });
    return response;
  }
  catch (err) {
    console.log(err);
  }
}



